.right {
  flex: 1.3;
  display: flex;
  flex-direction: column;
  position: -webkit-sticky;
  position: sticky;
  top: 50px;
}
.right .rightTop {
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 17px;
  font-weight: 500;
  background: white;
  border: 1px solid #E5E5E5;
  border-radius: 50px;
}
.right .rightTop .item {
  width: 100%;
  padding: 0 10px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.right .rightTop .selected {
  background: #F1E153;
  border-radius: 50px;
}
.right .rightBottom {
  min-height: 300px;
  background: #FFFFFF;
  display: flex;
  flex-direction: column;
  align-content: center;
  width: 50%;
  margin: auto;
  padding: 10px 50px;
  font-weight: 400;
  font-size: 25px;
  margin-top: 20px;
  border-radius: 15px;
}
.right .rightBottom .btn {
  background: #1A2459;
  box-shadow: 0px 15px 30px rgba(26, 36, 89, 0.2);
  border-radius: 15px;
  width: 70%;
  height: 35px;
  color: white;
  font-weight: 700;
  font-size: 20px;
  margin: 10px 0;
  cursor: pointer;
}

.inputItem {
  margin-bottom: 15px;
  display: flex;
  flex-direction: column;
  font-weight: 600;
  font-size: 17px;
  width: 70%;
}
.inputItem .desc {
  color: #736c6c;
}
.inputItem .input {
  outline: none;
  background: white;
  border: 1px solid #9E9E9E;
  border-radius: 5px;
  height: 35px;
  padding: 5px 10px;
  font-weight: 400;
  font-size: 16px;
  margin-top: 5px;
}
.inputItem .radioInput {
  display: flex;
}
.inputItem .radioInput .radioInputItem {
  margin: 10px 0;
  margin-right: 10px;
  font-size: 16px;
}
.inputItem .radioInput .radioInputItem label {
  margin-left: 10px;
}
.inputItem .radioInput .radioInputItem input {
  cursor: pointer;
}
.inputItem textarea {
  min-height: 100px;
  resize: none;
}/*# sourceMappingURL=YogaForm.css.map */